            font-size: 25px;
            display: block;
            margin: 0 0 20px 0;
            padding: 0 0 0 0px;
            text-align: center;
        }
    }

    .privacy-box {
        height: 400px;
        overflow-y: scroll;

        p,li,a {
            color: white;
            font-size: 15px;
            font-family: $hevleticaLt;
            text-align: left;
        }

        p,ul,ol {
            margin: 0 25px 15px 0;
        }
    }

    .modal-row__input{
        margin: 0 0 20px 0;
        input{
            border: 1px solid #323d3c;
            height: 50px;
            width: 100%;
            line-height: 50px;
